.game-display-item {
  width: 100%;
  flex-flow: row;
  justify-content: start;
  margin: 32px 4px 4px;
  display: flex;
}

.game-display-item img {
  width: 256px;
  height: 256px;
}

.game-display-item-description {
  text-align: left;
  margin-left: 24px;
  display: block;
}

.game-display-item-description h3 {
  margin: 0;
  padding: 0;
  font-size: 36px;
}

.game-display-item-description h4 {
  margin: 0;
  padding: 0;
  font-size: 22px;
}

.game-display-item-description p, .game-display-item-description a {
  margin: 0;
  padding: 0;
  font-size: 18px;
}

@media only screen and (max-width: 768px) {
  .game-display-item img {
    width: 192px;
    height: 192px;
  }
}

